
  .nav-wrapper {
    display: flex;
    justify-content: space-between;
    padding: 40px;
    color: rgb(155, 123, 65);
    background-image: url("https://pngimage.net/wp-content/uploads/2018/06/star-field-png-5.png");
    background-repeat: no-repeat;
    border: 1px solid black;
  }
  .right-side{
    display: flex;
  }
  
  .left-side {
    display: flex;
  }
  
  .nav-wrapper > .right-side > div {
    margin-right: 20px;
    text-transform: uppercase;
    font-size: 0.9rem;
  }
  
  .nav-wrapper > .left-side > div {
    margin-right: 20px;
    text-transform: uppercase;
    font-size: 0.9rem;
  }
  
  .nav-link-wrapper {
    height: 22px;
    border-bottom: 1px solid transparent;
    transition: border-bottom 0.5s;
  }
  
  .active-nav-link {
    border-bottom: 1px solid white;
  }
  
  .nav-link-wrapper a {
    color: wheat;
    text-decoration: none;
    transition: color 0.5s;
  
  }
  
  
  .nav-link-wrapper div:hover {
    border-bottom: 1px solid white
  }
  
  .nav-link-wrapper a:hover {
    color: rgb(161, 126, 59)
  }
  
  #nasaImg{
    max-width: 140px;
    position: relative;
    bottom: 45px;
    right: -50px;
    border-radius: 80px;
  
  
  }
  